.header-profile {
  display: none;
  position: absolute;
  background: $bg-1;
  z-index: $ui-index-3;
  transition: transform 0.5s linear;
  top: 4.6rem;
  right: 5.6rem;
  border-radius: .8rem;

  &__inner {
    padding: 1.6rem 0;
    background: $bg-gray;
    min-width: 21rem;
    border-radius: .8rem;
  }
  &__min-title {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-secondary;
    margin-bottom: 1.6rem;
    padding: 0 1.6rem;
  }
  &__items {
    display: flex;
    flex-direction: column;
    margin: 1.6rem 0;
  }
  &__item {
    width: 100%;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-main;
    display: flex;
    align-items: center;
    gap: .8rem;
    font-weight: 700;
    padding: 0.5rem 1.6rem;
    background-color: transparent;
    @include hover {
      background-color: $bg-modal;
    }
    svg {
      width: 2rem;
      height: 2rem;
    }
  }
  &__log-out {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    color: $red;
    display: flex;
    gap: .8rem;
    margin: 0 1.6rem;
    padding-top: 1.6rem;
    border-top: 1px solid $text-black-secondary;
    svg {
      width: 2rem;
      height: 2rem;
    }
  }
}
.header-profile.active {
  display: block;
}