.input {
    padding: 1.1rem 1.2rem 1.2rem 1.6rem;
    font-size: 1.2rem;
    font-weight: 700;
    line-height: 140%;
    color: $text-main;
    border: 0;
    background-color: $bg-gray;
    border-radius: .4rem;
    width: 100%;

    &::placeholder {
        font-weight: 400;
        color: $text-gray;
    }
    &:focus-visible {
        border: 0;
    }
    
    &__wrap {
        &--flex {
            @include flex-center;
            gap: 8px;
        }
    }
    &__block + &__block{
        margin-top: 2.4rem;
    }

    /* &__eye {
        content: '';
        position: absolute;
        @include svg-mask('eye-open2');
        right: 16px;
        top: 12px;
        width: 16px;
        height: 16px;
        background-color: $text-gray;
        z-index: $ui-index-2;
        padding: 0;
        border: 0;
        cursor: pointer;
        &.active {
            @include svg-mask('eye-closed2');
        }
        @include hover {
            background-color: $text-secondary;
        }
    } */
    &.error {
        border: 1px solid $red;
    }
    &__label {
        font-weight: 700;
        font-size: 14px;
        & span {
            color: $red;
        }
    }
    &__text {
        margin-top: 8px;
        color: $text-secondary;
        font-size: 12px;
        & a {
            color: $green;
        }
    }
    &__code {
        color: $green;
        border: 0;
        background-color: unset;
        font-size: 12px;
        font-weight: 700;
        position: absolute;
        right: 16px;
        top: 13px;
        padding: 0;
        z-index: $ui-index-2;
        cursor: pointer;
        &:disabled, &.disabled {
            color: $gray;
            cursor: auto;
        }
    }
    &__error {
        margin-top: 8px;
        font-size: 12px;
        color: $red;
        display: none;
    }
    &__hint {
        margin-top: 8px;
        @include desktop {
            margin-top: 0;
            position: absolute;
            top: 11px;
            right: 11px;
            width: 20px;
            height: 20px;
            @include svg-bg('question');
            cursor: help;
            overflow: visible;
            &:hover ul {
                display: block;
            }
        }
        & ul {
            font-size: 10px;
            padding: 8px;
            background-color: $bg-block;
            border-radius: 4px;
            @include desktop {
                display: none;
                position: absolute;
                z-index: $ui-index-1;
                top: calc(100% + 16px);
                width: 240px;
                left: calc(50% - 120px);
                background-color: $bg-gray;
                border: 1px solid $text-gray;
                &::before {
                    content: '';
                    position: absolute;
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 0 8px 8px 8px;
                    border-color: transparent transparent $text-gray transparent;
                    left: calc(50% - 8px);
                    top: -7px;
                    @include svg-mask('triangle');
                    mask-size: contain;
                    background-color: $text-gray;
                    transform: rotate(180deg);
                }
            }
        }
        & li {
            position: relative;
            padding-left: 14px;
            &::before {
                content: '';
                position: absolute;
                top: 6px;
                left: 6px;
                width: 2px;
                height: 2px;
                background-color: $text-main;
                border-radius: 50%;
            }
        }
    }
    &__select-search {
        position: relative;
        margin: 0 8px 8px;
        & input {
            padding: 9px 8px;
            color: $text-main;
            background-color: $bg-modal;
            border-radius: 4px;
            width: 100%;
            outline: 0;
            border: 0;
            &::placeholder {
                color: $text-gray;
            }
        }
        &::before {
            content: '';
            position: absolute;
            z-index: $ui-index-2;
            top: calc(50% - 8px);
            width: 16px;
            height: 16px;
            right: 8px;
            @include svg-mask(search);
            background-color: #6e7583;
            mask-size: 34px;
        }
    }
    &__select-phone {
        width: auto;
        &-toggle {
            width: auto;
            display: inline-flex;
            align-items: center;
            gap: 8px;
            padding: 11px 48px 10px 16px;
            & span {
                display: none;
            }
        }
        &-dropdown {
            top: calc(100% + 2px);
            left: 0;
            width: 220px;
            max-height: 201px;
            padding: 8px 0;
        }
        &-option {
            display: grid;
            grid-template-columns: 20px 28px auto;
            gap: 8px;
            &.itc-select__option_selected {
                background-color: transparent;
            }
            &.hidden {
                height: 0;
                opacity: 0;
                overflow: hidden;
                padding: 0;
            }
        }
    }
}