}
.link-btn--simple {
  border: 1px solid #6d6e73;
  background: transparent;
}
.link-btn--green {
  background: #46b75b;
  border: 0;
}

.btn {
  display: block;
  border-radius: 4px;
  padding: 6px 15px;
  background-color: #46b75b;
  white-space: nowrap;
  font-size: 14px;
  line-height: 1.4;
  text-align: center;
  color: #f2f4f6;
  width: 100%;
  border: 1px solid transparent;
  outline: 0;
  cursor: pointer;
}
@media (hover: hover) {
  .btn:not(:disabled) {
    transition: all 0.1s linear;
  }
  .btn:not(:disabled):hover {
    background-color: #258d3d;
  }
}
.btn:disabled {
  pointer-events: none;
  cursor: not-allowed;
  background-color: #292a2f;
  color: #555C63;
}
.btn.disabled {
  pointer-events: none;
  cursor: not-allowed;
  background-color: #292a2f;
  color: #555C63;
}
.btn--wide {
  width: 100%;
  padding: 13px 15px;
  font-size: 16px;
  line-height: 1.3;
  white-space: wrap;
}
.btn--center {
  display: inline-block;
  font-size: 16px;
  width: auto;
  padding: 12px 30px;
}
.btn--small {
  width: auto;
  min-width: 60px;
  padding: 4px 12px;
  font-size: 12px;
  line-height: 2;
}
.btn--transparent {
  border: 1px solid #a2a9b6;
  background-color: transparent;
  color: #ffffff;
}
