  .btn--transparent:not(:disabled) {
    transition: all 0.1s linear;
  }
  .btn--transparent:not(:disabled):hover {
    opacity: 0.8;
    border: 1px solid #a2a9b6;
    background-color: transparent;
  }
}
.btn--red {
  border: 1px solid #ff4761;
  background-color: transparent;
  color: #ff4761;
}
@media (hover: hover) {
  .btn--red:not(:disabled) {
    transition: all 0.1s linear;
  }
  .btn--red:not(:disabled):hover {
    background-color: #ff4761;
    color: #ffffff;
  }
}

.btn-index {
  background: #5020f5;
  border: 1px solid transparent;
