.profile-security {  
	&__level {
    margin-top: 1.6rem;
    display: flex;
    justify-content: space-between;
    @include tablet {
      justify-content: flex-start;
    }
  }

  &__level-wrap {
    display: flex;
    align-items: center;
    gap: 0.8rem;

    @include tablet {
      @include flex-center;
      gap: 8px;
    }
    &--red {
      color: $red;
      & span:first-child {
        background-color: $red;
      }
    }
    &--yellow {
      color: $yellow;
      & span:not(:last-child){
        background-color: $yellow;
      }
    }
    &--green {
      color: $green;
      & span {
        background-color: $green !important;
      }
    }
  }
  &__level-label {
    color: $text-secondary;
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
  }
  &__level-name {
    font-size: 1.4rem;
    font-weight: 400;
    color: inherit;
  }
  &__level-icon {
    display: flex;
    gap: .4rem;
    margin-top: 0.4rem;
    & span {
      display: block;
      width: 4.1rem;
      height: .4rem;
      background-color: $gray-d;
    }
  }
}