html {
    font-size: 10px;
}

body {
    font-family: $sans;
    font-size: 14px;
    font-feature-settings: "tnum";
    font-variant: tabular-nums;
    line-height: 1.4;
    overflow-x: hidden;
}

.wrapper {
    overflow: hidden;
    min-height: 100vh;
    min-height: 100dvh;
    display: flex;
    flex-direction: column;
    background-color: $bg-1;
    color: $white;
}

.container {
    padding: 0 1.5rem;
    width: 100%;
    max-width: 120rem;
    margin: 0 auto;
    box-sizing: border-box;
    @include tablet {
        padding: 0 2rem;
    }
    @include desktop {
        padding: 0;
    }
    &--flex {
        @include flex-center;
    }

    &--relative {
        position: relative;
    }
}

.main {
    flex: 1 0 auto;
}

.swiper-button-prev, .swiper-button-next {
    width: 43px;
    height: 43px;
    background-color: rgba(8, 10, 17, 0.698);
    border-radius: 50%;
    @include svg-bg(slider-arrow);
    background-size: 60px;
    z-index: $ui-index-2;
    &::after {
        display: none;
    }
}
.swiper-button-prev {
    transform: rotate(180deg);
}
