.btn {
    display: block;
    border-radius: 4px;
    padding: 6px 15px;
    background-color: $green;
    white-space: nowrap;
    font-size: 14px;
    line-height: 1.4;
    text-align: center;
    color: $text-main;
    width: 100%;
    border: 1px solid transparent;
    outline: 0;
    cursor: pointer;
    &:not(:disabled) {
        @include hover {
            background-color: $green-hover;
        }
    }
    &:disabled {
        pointer-events: none;
        cursor: not-allowed;
        background-color: $bg-gray;
        color: #555C63;
    }
    &.disabled {
        pointer-events: none;
        cursor: not-allowed;
        background-color: $bg-gray;
        color: #555C63;
    }
    &--wide {
        width: 100%;
        padding: 13px 15px;
        font-size: 16px;
        line-height: 1.3;
        white-space: wrap;
    }
    &--center {
        display: inline-block;
        font-size: 16px;
        width: auto;
        padding: 12px 30px;
    }
    &--small {
        width: auto;
        min-width: 60px;
        padding: 4px 12px;
        font-size: 12px;
        line-height: 2;
    }
    &--transparent {
        border: 1px solid $gray;
        background-color: transparent;
        color: $white;
        &:not(:disabled) {
            @include hover {
                opacity: 0.8;
                border: 1px solid $gray;
                background-color: transparent;
            }
        }
    }
    &--red {
        border: 1px solid $red;
        background-color: transparent;
        color: $red;
        &:not(:disabled) {
            @include hover {
                background-color: $red;
                color: $white;
            }
        }
    }
}