@mixin svg-mask($img) {
    mask-image: url('../img/sprites/sprite.svg#'+$img);
    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: cover;
}
@mixin svg-bg($img) {
    background-image: url('../img/sprites/sprite.svg#'+$img);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
@mixin flex-center {
    display: flex;
    align-items: center;
}
@mixin flex-end {
    display: flex;
    align-items: flex-end;
}
@mixin flex-end-end {
    display: flex;
    align-items: flex-end;
    justify-content: end;
}
@mixin flex-stretch {
    display: flex;
    align-items: stretch;
}
@mixin flex-center-space {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
@mixin flex-center-center {
    display: flex;
    align-items: center;
    justify-content: center;
}
@mixin flex-column {
    display: flex;
    flex-direction: column;
}
@mixin flex-column-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
@mixin flex-column-center-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
@mixin flex-column-space {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
@mixin pseudo-right {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
}
@mixin pseudo-left {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
}
@mixin no-scrollbar {
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}
@mixin gray-scrollbar {
    scrollbar-color: #646567;
    -ms-overflow-style: none;
    &::-webkit-scrollbar {
        width: 4px;
        scrollbar-width: initial;
    }
    &::-webkit-scrollbar-track {
        background: none;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #646567;
        border-radius: 3px;
    }
}
@mixin change-colors {
    &.red {
        color: $red;
    }
    &.green {
        color: $green;
    }
}
@mixin hover {
    @media (hover: hover) {
        transition: all .1s linear;
        &:hover {
            @content;
        }
    }
}
@mixin tablet {
    @media (min-width: $tablet) {
      @content;
    }
}
@mixin tablet-big {
    @media (min-width: $tablet-big) {
      @content;
    }
}
@mixin desktop {
    @media (min-width: $desktop) {
      @content;
    }
}
@mixin desktop-md {
    @media (min-width: $desktop-md) {
      @content;
    }
}
@mixin desktop-big {
    @media (min-width: $desktop-big) {
      @content;
    }
}