.actions {
  margin-top: 9.6rem;
}
.actions__inner {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;

  @include tablet-big {
    flex-direction: row;
  }
}
.actions__item {
  background: linear-gradient(180deg, #292a2f 0%, #161a17 100%);
  padding: 2.4rem 2.4rem 3.2rem 2.4rem;
  border-radius: 2.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 2.4rem;
  position: relative;

  & * {
    position: relative;
    z-index: $ui-index-3;
  }
  &::before {
    position: absolute;
    content: '';
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    top: -1px;
    left: -1px;
    background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
    border-radius: 2.4rem;
  }
  &::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: linear-gradient(180deg, #292a2f 0%, #161a17 100%);
    border-radius: 2.4rem;
    z-index: 2;
  }
  
  @include tablet {
    flex: 1;
  }
}
.actions__title {
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 140%;
  text-align: center;
  color: $text-main;
}
.actions__text {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 140%;
  text-align: center;
  color: $text-secondary;
  padding-top: 2.4rem;
  border-top: solid 1px rgba(255, 255, 255, 0.1);
}