.footer {
  margin-top: 6.4rem;
  border-top: 1px solid $border-black;

  &__inner {
    padding-top: 2.6rem;
    padding-bottom: 2.4rem;
    text-align: center;

    @include desktop {
      display: flex;
      padding: 2.4rem 0;
      align-items: center;
    }
  }
  &__copyright {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-black-secondary;
    margin-bottom: 1.6rem;
    display: inline-block;

    @include desktop {
      margin-bottom: 0;
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    gap: .8rem;
    margin-bottom: 2.4rem;

    @include tablet {
      flex-direction: row;
      gap: 2.4rem;
      justify-content: center;
    }
    @include desktop {
      margin-left: auto;
      margin-right: 2.4rem;
      margin-bottom: 0;
    }

    a {
      font-weight: 400;
      font-size: 1.4rem;
      line-height: 140%;
      color: $text-secondary;
      @include hover {
        color: $text-gray;
      }
    }
  }
  &__social-links {
    display: flex;
    gap: .8rem;
    justify-content: center;

    img {
      @include desktop {
        width: 2.4rem;
      }
    }
  }
  &__social-link {
    width: 24px;
    height: 24px;
    & svg {
      width: 24px;
      height: 24px;
      fill: $text-main;
    }
    @include hover {
      & svg {
        fill: $green;
      }
    }
  }
}