.earn {
  margin-top: 9.6rem;
  position: relative;

  @include tablet-big {
    margin-top: 4.8rem;
  }

  &__inner {
    position: relative;
    border-radius: 2.4rem;
    padding: 4.8rem 2.4rem;
    z-index: $ui-index-1;
    border-radius: 2.4rem;
    & * {
      position: relative;
      z-index: $ui-index-3;
    }
    &::before {
      position: absolute;
      content: '';
      width: calc(100% + 2px);
      height: calc(100% + 2px);
      top: -1px;
      left: -1px;
      background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
      border-radius: 2.4rem;
    }
    &::after {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: linear-gradient(180deg, #292a2f 0%, #161a17 100%);
      border-radius: 2.4rem;
      z-index: 2;
    }
    @include tablet {
      padding: 4.8rem 4rem 4rem 4rem;
    }
    @include desktop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 2.4rem;
      padding: 0 4.8rem;
      align-items: center;
    }
  }

  &__intro {
    text-align: center;
    margin-bottom: 2.4rem;

    @include desktop {
      grid-area: 1 / 1 / 2 / 2;
      text-align: left;
      align-self: flex-end;
    }
  }
  &__title {
    margin-bottom: 1.6rem;

    @include desktop {
      text-align: left;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-secondary;

    @include desktop {
      max-width: 46rem;
    }
  }

  &__blocks {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.6rem;
    margin-bottom: 2.4rem;

    @include tablet {
      flex-direction: row;
      gap: 2.4rem;
      justify-content: center;
      margin-bottom: 3.2rem;
    }
    @include desktop {
      grid-area: 2 / 1 / 3 / 2;
    }
  }
  &__block {
    text-align: center;
    box-shadow: 0 5px 15px 0 rgba(70, 183, 91, 0.25);
    background: $green;
    border-radius: 1.6rem;
    padding: 2.4rem 1.6rem;
    min-width: 29.7rem;

    @include tablet {
      max-width: 18rem;
      min-width: auto;
    }

    &--blue {
      box-shadow: 0 5px 15px 0 rgba(80, 32, 245, 0.25);
      background: $text-blue;
    }
  }
  &__percent {
    font-weight: 700;
    font-size: 3.2rem;
    line-height: 140%;
    color: $text-main;
    margin-bottom: 0.8rem;
  }
  &__min-text {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-main;
  }
  &__plus {

  }

  &__imgs {
    display: flex;
    flex-direction: column;

    @include tablet {
      flex-direction: row;
      align-items: center;
      gap: 1.1rem;
      justify-content: center;
    }
    @include desktop {
      grid-area: 1 / 2 / 3 / 3;
    }
  }
  &__main-img {
    @include desktop {
      min-width: 36.2rem;
    }

    img {
      margin: 0 auto;
    }
  }
  &__scheme-img {
    @include desktop {
      min-width: 27.5rem;
    }

    img {
      margin: 0 auto;
    }
  }

  &__bg {
    position: absolute;
    width: 100%;
    bottom: 0;
    transform: translateY(50%);
    left: 0;
  }
}