.header {
  background-color: $bg-1;

  &__links-logout {
    margin-left: auto;
    display: none;
    gap: 2.4rem;
    margin-right: 2.4rem;

    @include tablet {
      display: flex;
    }
  }
  &__link-btn-simple,
  &__link-btn-green {
    display: none;

    @include tablet {
      display: block;
    }
  }
  &__link-btn-simple {
    margin-left: 2.4rem;
  }
  &__link-btn-green {
    margin-left: 1.6rem;
  }
}
.header__inner {
  display: flex;
  align-items: center;
  padding: 1.2rem 1.5rem;
  max-width: 192rem;
  margin: 0 auto;
  border-bottom: 1px solid $border-black;

  @include tablet {
    padding: 1.2rem 2rem;
  }
  @include desktop {
    padding: 1.2rem 1.6rem;
  }
}
.header__logo {
  height: 3.6rem;
}
.header__links {
  display: none;

  @include tablet {
    display: flex;
    align-items: center;
    gap: 1.6rem;
    margin-left: auto;
  }
}
.header__link {
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 140%;
  color: $text-main;
  @include hover { 
    color: $text-secondary;
    & svg {
      fill: $text-secondary;
    }
  }
  &--video {
    display: flex;
    align-items: center;

    svg {
      width: 2.4rem;
      height: 2.4rem;
      margin-right: 0.8rem;
      fill: $text-main;
      transition: all .1s linear;
    }
  }
}
.header__profile {
  display: none;

  @include tablet {
    display: block;
    width: 2.4rem;
    height: 2.4rem;
    margin: 0 1.6rem;
  }
}
.header__lang {
  width: 2.4rem;
  height: 2.4rem;
  margin-left: auto;
  margin-right: 1.6rem;

  @include tablet {
    margin: 0;
  }

  svg {
    fill: $text-main;
  }

  &--logout {
    margin-right: 2rem;
  }
}
.header__profile:hover,
.header__lang:hover {
  cursor: pointer;
}
.header__lang:hover .lang {
  display: block;
}
.header__burger {
  width: 1.5rem;
  height: 1.2rem;

  @include tablet {
    display: none;
  }
}

@import "mobile-menu/mobile-menu";
@import "mobile-lang/mobile-lang";
@import "header-profile/header-profile";