.intro {
  margin-top: 4.8rem;
  @include tablet {
    margin-top: 9.6rem;
  }
  @include tablet-big {
    margin-top: 0;
    padding-top: 14rem;
    padding-bottom: 16.5rem;
    position: relative;
  }
  &__bg-coins {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;

    @include desktop-md {
      display: block;
    }
  }
  &__inner {
    @include tablet {
      text-align: center;
    }
    @include tablet-big {
      display: flex;
      align-items: center;
      position: relative;
      z-index: $ui-index-2;
    }
  }
  &__title {
    @include tablet {
      max-width: 54rem;
      margin-left: auto;
      margin-right: auto;
    }
    @include tablet-big {
      text-align: left;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 140%;
    text-align: center;
    color: $text-secondary;
    margin-bottom: 3.2rem;

    @include tablet {
      max-width: 45rem;
      margin-bottom: 4rem;
      margin-left: auto;
      margin-right: auto;
      font-size: 2rem;
    }
    @include tablet-big {
      text-align: left;
      margin-left: 0;
      margin-right: 0;
    }
  }
  &__btn {
    margin-left: auto;
    margin-right: auto;

    @include tablet {
      max-width: 22.8rem;
    }
    @include tablet-big {
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 0;
    }
  }
  &__img {
    z-index: $ui-index-1;
    position: relative;
    margin: 0 auto;
    max-height: 620px;
    @include tablet-big {
      height: 100%;
    }
    @include desktop-md {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
    @include desktop-big {
      left: 0;
      transform: none;
    }
  }
  &__phones {
    position: relative;

    @include tablet-big {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
    }
  }
  &__bg {
    position: absolute;
    top: 0;
    right: -5.5rem;
    height: 100%;
    @include tablet-big {
      position: absolute;
      top: 0;
      right: -45rem;
      height: 100%;
      max-width: none;
    }
    @include desktop-md {
      position: relative;
    }
    @include desktop-big {
      right: 0;
    }
  }
}
