.mobile-menu {
  position: absolute;
  background: $bg-1;
  width: 100%;
  height: 100%;
  z-index: $ui-index-3;
  transform: translateX(100%);
  transition: transform 0.5s linear;

  @include tablet {
    display: none;
  }

  &__inner {
    padding-top: 2.4rem;
  }
  &__link {
    font-weight: 700;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-main;
    display: block;
    margin-bottom: 1.6rem;
  }
  &__dropdown {
    margin-bottom: 2.4rem;

    span {
      font-weight: 700;
      font-size: 1.4rem;
      line-height: 140%;
      color: $text-main;
      display: flex;
      gap: .4rem;
      align-items: center;
      transition: .5s;

      svg {
        width: .7rem;
        height: .7rem;
        fill: #FFF;
        transform: rotate(180deg);
        transition: .5s;
      }
    }

    ul {
      display: none;
      padding-top: 1.2rem;
      padding-left: 1.6rem;
      flex-direction: column;
      gap: .8rem;

      li a {
        font-weight: 400;
        font-size: 1.2rem;
        line-height: 140%;
        color: $text-secondary;
      }
    }
  }
  &__dropdown.active {
    span {
      color: $green;
      transition: .5s;

      svg {
        fill: $green;
        transform: rotate(0deg);
        transition: .5s;
      }
    }
    ul {
      display: flex;
    }
  }

  &__btn {
    border: 1px solid $text-black-secondary;
    border-radius: .4rem;
    padding: .6rem 1.2rem;
    width: 100%;
    font-weight: 700;
    font-size: 1.4rem;
    line-height: 140%;
    text-align: center;
    background: transparent;
    color: $text-main;
  }

  &__link-btn-simple {
    width: 100%;
    margin-bottom: 0.8rem;
  }
  &__link-btn-green {
    width: 100%;
    margin-bottom: 2.4rem;
  }
}
.mobile-menu.active {
  transform: translateX(0);
  transition: transform 0.5s linear;
}