.advants {
  margin-top: 9.6rem;

  @include tablet-big {
    margin-top: 4.8rem;
  }

  &__inner {
    @include tablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 2.4rem;
      align-items: center;
    }
    @include tablet-big {
      align-items: normal;
    }
  }

  &__intro {
    text-align: center;
    margin-bottom: 2.4rem;

    @include tablet {
      grid-area: 1 / 1 / 2 / 3;
    }
    @include tablet-big {
      grid-area: 1 / 1 / 2 / 2;
      text-align: left;
      align-self: flex-end;
    }
  }
  &__suptitle {
    font-weight: 700;
    font-size: 2rem;
    line-height: 140%;
    text-align: center;
    color: $green;
  }
  &__title {
    @include tablet-big {
      text-align: left;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-secondary;

    @include tablet-big {
      max-width: 46rem;
    }
  }

  &__items {
    display: flex;
    flex-direction: column;
    gap: 2.4rem;
    margin-bottom: 2.4rem;

    @include tablet {
      min-width: 38rem;
      margin-bottom: 0;
    }
    @include tablet-big {
      grid-area: 2 / 1 / 3 / 2;
    }
  }
  &__item {
    text-align: center;

    @include tablet {
      display: flex;
      align-items: flex-start;
      gap: 1.6rem;
      text-align: left;
    }
  }
  &__icon {
    display: inline-block;
    margin-bottom: 1.6rem;
    max-width: 5.6rem;
    width: 100%;

    @include tablet {
      margin-bottom: 0;
    }
  }
  &__min-title {
    font-weight: 700;
    font-size: 1.8rem;
    line-height: 140%;
    color: $text-main;
    margin-bottom: 0.3rem;
  }

  &__img {
    @include tablet-big {
      grid-area: 1 / 2 / 3 / 3;
      max-width: 50.1rem;
      justify-self: flex-end;
    }

    img {
      margin: 0 auto;
    }
  }
}