.partner {
  margin-top: 9.6rem;

  &__text {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    text-align: center;
    color: $text-secondary;
    margin-bottom: 3.2rem;

    @include tablet {
      margin-bottom: 4rem;
    }
    @include tablet-big {
      max-width: 56.8rem;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__btn {
    @include tablet {
      max-width: 31.9rem;
      margin: 0 auto;
    }
  }
}