.warning-block {
    margin-top: 16px;
    padding: 16px 16px 16px 52px;
    border-radius: 4px;
    background-color: $yellow;
    color: $text-warning-yellow;
    font-weight: 700;
    font-size: 12px;
    @include svg-bg('warning');
    background-size: 30px;
    background-position: 10px 50%;
}