.statics {
  margin-top: 9.6rem;

  @include tablet-big {
    margin-top: 4.8rem;
  }
  
  &__inner {
    @include tablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
    }
    @include tablet-big {
      align-items: normal;
      column-gap: 2.4rem;
      grid-template-columns: auto;
    }
  }
  &__title {
    @include tablet {
      grid-area: 1 / 1 / 2 / 3;
    }
    @include tablet-big {
      grid-area: 1 / 2 / 2 / 2;
      min-width: 62rem;
      align-self: flex-end;
      text-align: left;
    }
    @include desktop {
      max-width: 72rem;
    }
  }
  &__items {
    display: flex;
    flex-direction: column;
    gap: 2.4rem;
    margin-bottom: 2.4rem;

    @include tablet-big {
      grid-area: 2 / 2 / 3 / 3;
      flex-direction: row;
      gap: 4rem;
      margin-bottom: 0;
    }
  }
  &__item {
    text-align: center;

    @include tablet {
      text-align: left;
    }
  }
  &__number {
    font-weight: 700;
    font-size: 4.8rem;
    line-height: 140%;
    color: $green;
    display: flex;
    align-items: flex-start;
    justify-content: center;

    @include tablet {
      justify-content: flex-start;
    }

    span {
      font-weight: 400;
      font-size: 2rem;
      line-height: 140%;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-secondary;
  }
  &__img {
    @include tablet {
      min-width: 38.8rem;
    }
    @include tablet-big {
      grid-area: 1 / 1 / 3 / 2;
      max-width: 40rem;
    }

    img {
      margin: 0 auto;
    }
  }
}