.breadcrumbs {
    list-style-type: none;
    display: flex;
    align-items: center;
    padding: 0;
    flex-wrap: wrap;
    row-gap: 2px;
    margin-bottom: 8px;
    & li {
        color: $text-main;
        &:not(:last-child) {
            color: $text-secondary;
            position: relative;
            padding-right: 22px;
            &::after {
                position: absolute;
                content: '/';
                right: 8px;
                width: 6px;
            }
        }
        & a {
            color: inherit;
            @include hover {
                color: $text-gray;
            }
        }
    }
}