.itc-select {
  position: relative;
  width: 100%;
}

.itc-select__toggle {
  position: relative;
  display: block;
  width: 100%;
  padding: 16px 12px;
  font-size: 12px;
  line-height: 1.4;
  background-color: transparent;
  border: 1px solid #363738;
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
  color: $text-main;
  transition: all .1s ease-in-out;    
}

.itc-select__toggle::after {
  position: absolute;
  content: '';
  background-image: initial;
  background-color: $text-gray;
  width: 16px;
  height: 16px;
  right: 8px;
  top: calc(50% - 8px);
  z-index: $ui-index-2;
  @include svg-mask('triangle');
  mask-size: 14px;
  transition: all .1s ease-in-out;    
}

.itc-select__toggle:focus {
  outline: none;
}
.itc-select_show .itc-select__toggle::after {
  transform: rotate(180deg);
}

.itc-select__dropdown {
  @include gray-scrollbar;
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 2;
  display: none;
  max-height: 10rem;
  overflow-y: auto;
  background-color: #292a2f;
  border-radius: 4px;
  box-shadow: 0px 0px 26.899999618530273px 0px #00000087;
}

.itc-select_show .itc-select__dropdown {
  display: block;
}

.itc-select_show .itc-select__backdrop {
  display: block;
}

.itc-select__options {
  margin: 0;
  padding: 0;
  list-style: none;
}

.itc-select__option {
  padding: 0.375rem 0.75rem;
}

.itc-select__option_selected {
  background-color: #e1f5fe;
}

.itc-select__option:hover {
  background-color: #18191C;
  cursor: pointer;
  transition: 0.1s background-color ease-in-out;
}
