.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: $ui-index-10;
    background-color: rgba(17, 20, 18, 0.7);
    &.hide {
        display: none;
    }
    &.block {
        @include flex-column-center-center;
        & .popup-inner {
            animation: grow .2s ease-out;
        }
    }
    &.decrease {
        & .popup-inner {
            animation: decrease .2s ease-out;
        }
    }
    &-inner {
        width: 345px;
        max-width: calc(100% - 32px);
        max-height: calc(100vh - 32px);
        margin: 16px;
        border-radius: 8px;
        background: $bg-gray;
        color: $text-main;
        overflow-y: auto;
        padding: 24px;
        transition: transform .2s ease-out;
        &--overflow {
            @include desktop {
                overflow: visible;
            }
        }
        &--nopadding {
            padding: 0;
        }
        @include tablet {
            width: 460px
        }
    }
    &-head {
        @include flex-center-space;
    }
    &__title {
        font-size: 16px;
        font-weight: 700;
    }
    &__close {
        width: 20px;
        height: 20px;
        @include svg-mask(close);
        background-color: $text-secondary;
        mask-size: cover;
        cursor: pointer;
    }
    &-body {
        margin-top: 24px;
        font-size: 12px;
        & a {
            color: $green;
        }
    }
    &-footer {
        margin-top: 32px;
        &--flex {
            @include flex-center-space;
        }
    }
    &__back {
        font-weight: 600;
        font-size: 12px;
        color: $green;
        @include hover {
            color: $green-hover
        }
    }
    &__btns {
        @include flex-end-end;
        gap: 8px
    }
    &__btn {
        padding: 8px 15px;
        font-size: 14px;
        font-weight: 600;
        width: auto;
    }
    &__subtitle {
        margin-top: 24px;
        color: $text-main;
        font-weight: 700;
        font-size: 14px;
        & + .popup__text {
            margin-top: 8px;
        }
    }
    &__text {
        margin-top: 24px;
        color: $text-secondary;
        font-size: 12px;
        &--lessmargin {
            margin-top: 16px;
            & + * {
                margin-top: 16px;
            }
        }
    }
}

@keyframes grow {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(100%);
    }
}
@keyframes decrease {
    0% {
        transform: scale(100%);
    }
    100% {
        transform: scale(0);
    }
}