.link-btn {
    display: block;
    font-weight: 700;
    font-size: 1.4rem;
    line-height: 140%;
    text-align: center;
    color: $text-main;
    border-radius: .4rem;
    padding: .6rem 1.2rem;
    outline: none;

    &--simple {
        border: 1px solid $text-gray;
        background: transparent;
    }
    &--green{
        background: $green;
        border: 0;
    }
}