.btn-index {
  background: $text-blue;
  border: 1px solid transparent;
  outline: 0;  
  display: flex;
  justify-content: center;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 140%;
  color: $text-main;
  padding: 2rem 0;
  width: 100%;
  border-radius: .8rem;
  box-shadow: 0 18px 36px -18px rgba(80, 32, 245, 0.25), 0 30px 60px -12px rgba(80, 32, 245, 0.25);
  margin-bottom: 6.4rem;
  @include hover {
    box-shadow: initial;
    background-color: $blue-hover;
  }
  svg {
    width: 1.8rem;
    height: 1.8rem;
    margin-left: 0.8rem;
  }
}