.mobile-lang {
  position: absolute;
  background: $bg-1;
  width: 100%;
  height: 100%;
  z-index: $ui-index-3;
  transform: translateX(100%);
  transition: transform 0.5s linear;

  @include tablet {
    top: 4.6rem;
    right: 1.6rem;
    border-radius: .8rem;
    width: auto;
    height: auto;
    transform: none;
    display: none;

    &--logout {
      right: 8rem;
    }
  }

  &__inner {
    padding-top: 2.4rem;

    @include tablet {
      padding: .8rem 0;
      background: $bg-gray;
      min-width: 24rem;
      border-radius: .8rem;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 1.6rem;
    line-height: 140%;
    color: $text-main;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 2.4rem;
    margin-bottom: 2.4rem;
    position: relative;

    @include tablet {
      display: none;
    }

    svg {
      width: 2rem;
      height: 2rem;
    }

    &::after {
      content: '';
      height: 1px;
      width: 120%;
      background: $border-black;
      position: absolute;
      bottom: 0;
      left: -1.5rem;
    }
  }
  &__items {
    display: flex;
    flex-direction: column;
    gap: 1.6rem;

    @include tablet {
      gap: 0;
    }
  }
  &__item {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    color: $text-main;
    display: flex;
    gap: .8rem;

    @include tablet {
      width: 100%;
      padding: .8rem 0 .8rem 1.6rem;
    }
  }
  &__item:hover {
    background: $bg-modal;
    cursor: pointer;
  }
  &__item.active {
    color: $green;
  }
}
.mobile-lang.active {
  transform: translateX(0);
  transition: transform 0.5s linear;

  @include tablet {
    transform: none;
    display: block;
  }
}
.mobile-lang .container {
  @include tablet {
    padding: 0;
  }
}