.pagination {
    margin-top: 24px;
    @include flex-center-center;
    gap: 8px;
    &__item {
        @include flex-column-center-center;
        &--dots {
            width: 40px;
            height: 40px;
            @include hover {
                background-color: transparent;
            }
        }
    }
    &__btn {
        color: $text-main;
        font-weight: 700;
        border-radius: 50%;
        border: 1px solid $bg-gray;
        background-color: transparent;
        padding: 0;
        width: 40px;
        height: 40px;
        cursor: pointer;
        @include hover {
            background-color: $bg-gray;
        }
        .active & {
            cursor: default;
            background-color: $green;
            border: 1px solid $green;
        }
        &--back, &--next {
            @include svg-bg('arrow-white');
            background-size: 48px;
        }
        &--next {
            transform: rotate(180deg);
        }
    }
}