.application {
  margin-top: 2.4rem;
  margin-bottom: 2.4rem;

  &-steps {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    padding-top: 3rem;
    max-height: 17rem;
    overflow: hidden;
    position: relative;

    &::after {
      content: '';
      background: linear-gradient(180deg, rgba(21, 21, 21, 0) 1%, #151515 100%);
      position: absolute;
      height: 6.4rem;
      width: 100%;
      bottom: 0;
    }
    
    &__item {
      font-weight: 700;
      font-size: 1.2rem;
      line-height: 140%;
      text-align: center;
      color: $text-gray;
      border: 1px solid $bg-gray;
      padding: 1.6rem 0;
      border-radius: .4rem;
      background: $bg-1;
    }
    &__item.active {
      color: $green;
      border: 1px solid $green;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 140%;
    text-align: center;
    color: $text-main;
    margin-bottom: 2.4rem;
  }

  &__text {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 140%;
    text-align: center;
    color: $text-secondary;
  }

  &__form {
    margin-top: 2.4rem;
  }
}